@font-face {
  font-family: NotesEsaBold;
  src: url('public/fonts/notesesabol-webfont.woff');
  font-weight: bold;
}

@font-face {
  font-family: NotesEsaReg;
  src: url('public/fonts/notesesareg-webfont.woff');
  font-weight: normal;
}

@font-face {
  font-family: Open-sans;
  src: url('public/fonts/OpenSans-Regular.ttf');
  font-weight: normal;
}

@font-face {
  font-family: icomoon;
  src: url('public/fonts/icomoon.woff');
  font-weight: normal;
}

[class^="esa-icon-"], [class*=" esa-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.esa-icon-arrow-right {
  position: absolute;
  top: 25%;
  right: 20px;
  transform-origin: center;
  transition: transform 0.2s cubic-bezier(0.215, 0.610, 0.355, 1);
}

.esa-icon-arrow-right:before {
  content: "\e900";
  font-size: 0.7rem;
}

* {
  box-sizing: border-box;
}
html {
  height: 100%;
  margin: 0;
  min-height: 100%;
  position: relative;
  font-family: Arial, Helvetica, sans-serif;
}
body { 
    position: relative;
    font-family: Arial, Helvetica, sans-serif;
    max-height: 100%;
    height: 100%;
    overflow: hidden;
}

canvas {
  max-height:100vh;
}

.ESA__heading {
  position: absolute;
  top:0;
  left:0;
  right: 0;
  align-items: center; 
  display: flex;
  flex-direction: row;
  height: 61px;
  justify-content: space-between;
  padding: 0 15px;
  z-index: 999;
}

.ESA__heading__text {
  font-size: 15px;
  font-family: NotesEsaBold;
  color:#FFFFFF;
  text-transform: capitalize;
}

h1.model-name {
  font-family: NotesEsaBold;
  font-size: 32px;
  color: #FFFFFF;
}

.back-to-website {
  background: none;
  border:none;
  color:#FFFFFF;
  padding:0;
  display: inline;
}

.back-to-website span {
  vertical-align: middle;
  font-size: 12px;
  font-family: Open-sans;
  padding:0 5px;
}

.back-to-website .arrow-left {
  width:26px;
  height: 26px;
  background: rgba(255,255,255, 0.3);
  display: inline-block;
  vertical-align: middle;
  position: relative;
}

.back-to-website .arrow-left::after{
  content: '';
  position: absolute;
  right: 0px;
  top: 50%;
  display: block;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  width: 8px;
  height: 8px;
  transform-origin: center;
  transform: translate(-50%, -50%) rotate(-225deg);
}

button#deploy {
  position: absolute;
  top: 90%;
  left: 50%;
  font-family: 'NotesEsaBold';
  text-transform: uppercase;
  padding: 15px 15px;
  transform: translate(-50%, -50%);
  width: 300px;
  font-size: 1.5rem;
  background: rgb(0 0 0 / 15%);
  box-shadow: 0 8px 32px 0 rgb(90 90 90 / 15%);
  backdrop-filter: blur( 7.5px );
  -webkit-backdrop-filter: blur( 7.5px );
  border-radius: 3px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );
  color: #00ffff;
  transition: background 0.15s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

button#deploy:hover {
  background: rgb(255 255 255 / 15%);
}

/*
LOADEDR 
*/
.loader-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #232323;
  z-index: 1;
}

.lds-ellipsis {
  display: inline-block;
  position: absolute;
  width: 80px;
  height: 80px;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #fff;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}


footer {
  position: absolute;
  bottom:0;
  left:0;
  right: 0;
  height:30px;
  text-align: right;
  padding:5px 15px
}

footer span{
  font-size: 12px;
  font-family: Open-sans;
  color:rgba(200,200,200,0.5);
}

@media (max-width:768px){
  body {
    
  }
  h1.model-name {
    font-size: 24px;
    left:15px;
    top:20%;
  }

  .back-to-website {
    left:15px;
    top:13%;
  }
}